.Footer .Caption {
  background: rgb(149, 149, 149); /* Old browsers */
  background: -moz-linear-gradient(
    top,
    rgba(149, 149, 149, 1) 0%,
    rgba(13, 13, 13, 1) 46%,
    rgba(1, 1, 1, 1) 50%,
    rgba(10, 10, 10, 1) 53%,
    rgba(78, 78, 78, 1) 76%,
    rgba(56, 56, 56, 1) 87%,
    rgba(27, 27, 27, 1) 100%
  ); /* FF3.6-15 */
  background: -webkit-linear-gradient(
    top,
    rgba(149, 149, 149, 1) 0%,
    rgba(13, 13, 13, 1) 46%,
    rgba(1, 1, 1, 1) 50%,
    rgba(10, 10, 10, 1) 53%,
    rgba(78, 78, 78, 1) 76%,
    rgba(56, 56, 56, 1) 87%,
    rgba(27, 27, 27, 1) 100%
  ); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(
    to bottom,
    rgba(149, 149, 149, 1) 0%,
    rgba(13, 13, 13, 1) 46%,
    rgba(1, 1, 1, 1) 50%,
    rgba(10, 10, 10, 1) 53%,
    rgba(78, 78, 78, 1) 76%,
    rgba(56, 56, 56, 1) 87%,
    rgba(27, 27, 27, 1) 100%
  ); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#959595', endColorstr='#1b1b1b',GradientType=0 ); /* IE6-9 */
  height:120%;
  color: silver;
  font-family: "Edwardian Script ITC", "Brush Script MT", "Zapfino", "Lucida Handwriting", cursive;
  border-radius: 10px;
  border: 0.1rem blanchedalmond Outset;
  padding:0;
  margin:0;
}
.Footer .Caption h5 {
width:100%;
font-size-adjust: inherit;  
margin: 0;
padding: 0.3rem 0.5rem 0 0.2rem;
  font-size: 2rem;
  line-height:1.5rem;
  text-transform: capitalize;
}
